


















import { mapState } from 'vuex';
import { updUserPassword } from '@/request/API/index.js';
export default {
	data() {
		return {
			password1: '', //旧密码
			password2: '', //新密码
			password3: '' //第二次输入
		};
	},
	onLoad() {},
	methods: {
		passWordChange() {
			if (!this.password1) {
				uni.showModal({
					title: '',
					content: '旧密码不能为空',
					showCancel: false
				});
				return;
			}
			if (!this.password2) {
				uni.showModal({
					title: '',
					content: '新密码不能为空',
					showCancel: false
				});
				return;
			}
			if (!this.password3) {
				uni.showModal({
					title: '',
					content: '请再次输入新密码',
					showCancel: false
				});
				return;
			}
			if (this.password2 != this.password3) {
				uni.showModal({
					title: '',
					content: '两次输入密码不一致',
					showCancel: false
				});
				return;
			}
			let params = {
				userId: this.userId,
				password: this.password1,
				newPassword: this.password2
			};
			updUserPassword(params).then(res => {
				if (res.data.code == 0) {
					uni.showToast({
						title: '修改密码成功',
						mask: false,
						duration: 1500
					});
					setTimeout(res => {
						uni.navigateTo({
							url: '/pages/logn/logn'
						});
					}, 1500);
				} else {
					uni.showModal({
						title: '',
						content: res.data.msg,
						showCancel: false
					});
				}
			});
		}
	},
	computed: {
		...mapState(['userId'])
	}
};
